.container {
	width: 100%;
}
.mt {
	margin-top: 20px;
}
.newBankContainer {
	width: 940px;
	padding: 30px;
	margin: 0 auto;
	background: white;
	-webkit-box-shadow: 0 0 1rem #aaa;
	        box-shadow: 0 0 1rem #aaa;
}
.questions {
	width: 100%;
}
.questionType {
	width: 100%;
	margin: 15px 0;
	-webkit-transition: height .4s;
	transition: height .4s; 
	overflow: hidden;
}
.addBank {
	width: 100%;
	height: 60px;
	margin-top: 15px;
	cursor: pointer;
	line-height: 60px;
	background: #ebf7ff;
	border: 1px solid #3af7bb;
}
.operation {
	width: 100%;
	margin: 15px 0;
	height: 30px;
}
.questionList {
	width: 96%;
	padding: 15px 2%;
	margin: 20px 0;
	background: #ccc;
	text-align: left;
}
.selector ul {
	  list-style: upper-alpha;
}

.edit {
	width: 100%;
	height: 50px;
	line-height: 50px;
	text-align: center;
	font-size: 20px;
	cursor: pointer;
}
.edit:hover {
	background: #ebf7ff;
}
.input {
	width: 100%;
	height: 50px;
	line-height: 50px;
	text-align: center;
	font-size: 20px;
	border:0;
	outline: 1px dashed #ccc;
}
.hide {
	display: none;
}
.show {
	display: block;
}

.line {
	width: 100%;
	padding-top: 10px;
}
.defaultLine {
	border-bottom: 1px solid #eee;
}
.defaultSize {
	border-bottom-width: 1px;
}
.middleSize {
	border-bottom-width: 2px;
}
.bigSize {
	border-bottom-width: 3px;
}
.linedashed {
	border-bottom-style: dashed;
}
.linesolid {
	border-bottom-style:solid;
}
.linesuccess {
	border-bottom-color: #19be6b;
}
.linedefault {
	border-bottom-color: #eee;
}
.linewarning {
	border-bottom-color: #f90;
}
.lineerror {
  border-bottom-color: #ed3f14;
}

.modalContainer {
	z-index: 2;
	position: fixed;
	top: 20%;
  	left: 25%;
	width: 50%;
	max-height: 60%;
	padding-bottom: 20px;
  	border-radius: .5rem;
  	-webkit-box-shadow: 0 0 5px #555;
  	        box-shadow: 0 0 5px #555;
  	background-color: #fff;
  	overflow: auto;
}
.modalHeader {
	width: 96%;
	height: 40px;
	padding:0 2% ;
	line-height: 40px;
  	border-radius: .5rem;
  	background-color: #f7f7f7;
}
.modalHeader .title{
	float: left;
	font-size: 14px;
	font-weight: 400;
}
.modalHeader .close{
	float: right;
	cursor: pointer;
}
.modalHeader .close:hover{
	-webkit-transform: rotate(90deg);
	        transform: rotate(90deg);
	color:#19be6b;
}
.modalBody {
	width: 96%;
	padding:0 2% ;
}
.modalBody .item {
	width: 100%;
	margin: 10px 0;
	text-align: left;
}
.modalBody .item label{
	width: 10%;
	display: inline-block;
	color: #495060;
	font-size: 14px;
}
.modalBody .item input{
	width: 60%;
	height: 25px;
    line-height: 1.5;
    padding: 4px 7px;
    font-size: 12px;
	border: 1px solid #dddee1;
  	border-radius: 4px;
  	color: #495060;
}
.modalBody .item input.questionInput {
	outline: 0;
	/*border-color: #f90;*/
}
.modalBody .item input:focus{
	outline: 0;
	border-color: #57a3f3;
	-webkit-box-shadow: 0 0 0 1px rgba(45,140,240,.2);
	        box-shadow: 0 0 0 1px rgba(45,140,240,.2);
}
.modalBody .item input:hover{
	border-color: #57a3f3;
}
.modalBody .item input.right{
	outline: 0;
	border-color: #19be6b;
    -webkit-box-shadow: 0 0 0 1px rgba(45,140,240,.2);
            box-shadow: 0 0 0 1px rgba(45,140,240,.2);
}
.modalBody .item-no-label {
	margin-left: 25%;
	height: 35px;
}
.modalBody .setting {
	font-size: 12px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;  
	cursor: pointer;
	margin-left: 5px;
}
.m-show {
	display: block;
}
.m-hide {
	display: none;
}
.tips {
	width: 96%;
	padding:10px 2% ;
	margin: 10px 0;
	text-align: left;
	border-left: 5px solid #19be6b;
	background: #ececec;
}
.fonttip {
	font-size: 14px;
	color: #495060;
}
.delFlag {
	width: 30px;
	cursor: pointer;
	text-align: center;
	display: inline-block;
}

.header{
	width: 100%;
